"use server";

import { revalidatePath } from "next/cache";
import { redirect } from "next/navigation";

import { createClient } from "@/utils/supabase/server";

//REGISTER

export async function signup(formData: FormData) {
  const supabase = await createClient();

  const data = {
    email: formData.get("email") as string,
    password: formData.get("password") as string,
    role: (formData.get("role") as string) || "editor",
  };

  // Step 1: Use Admin API to create a user without logging in
  const { data: user, error } = await supabase.auth.admin.createUser({
    email: data.email,
    password: data.password,
    email_confirm: true, // Mark email as confirmed, if relevant
  });

  if (error) {
    throw new Error("Signup failed: " + error.message);
  }

  const userId = user?.user?.id;

  if (!userId) {
    throw new Error("Failed to retrieve user ID after signup.");
  }

  // Step 2: Create a profile in the "profiles" table using the service role
  const serviceSupabase = await createClient();
  const { error: profileError } = await serviceSupabase
    .from("profiles")
    .insert([
      {
        id: userId,
        role: data.role,
      },
    ]);

  if (profileError) {
    throw new Error("Failed to create profile: " + profileError.message);
  }

  console.log("User signed up without session and profile created.");

  // Optionally, revalidate paths or do other post-signup actions
  revalidatePath("/", "layout");
}

//LOGOUT

export async function signOut() {
  const supabase = await createClient();

  await supabase.auth.signOut();

  revalidatePath("/", "layout");
  redirect("/login");
}

// ADMIN CHECK

export async function isAdmin() {
  const supabase = await createClient();

  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError) {
    throw new Error("Failed to authenticate user: " + authError.message);
  }

  if (!user) {
    return false;
  }

  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", user.id)
    .single();

  if (profileError) {
    throw new Error("Failed to fetch user role: " + profileError.message);
  }

  return profile.role === "admin";
}

// ADMIN GET ALL USERS

export async function getAllUsers() {
  const supabase = await createClient();

  const admin = await isAdmin();

  if (!admin) {
    throw new Error("Unauthorized: Only admins can access this resource");
  }

  // Hent alle brugere
  const {
    data: { users },
    error: fetchError,
  } = await supabase.auth.admin.listUsers();

  if (fetchError) {
    throw new Error("Failed to fetch users: " + fetchError.message);
  }

  return users || [];
}

export async function getUserRole() {
  const supabase = await createClient();

  const {
    data: { user },
    error: authError,
  } = await supabase.auth.getUser();

  if (authError) {
    throw new Error("Failed to authenticate user: " + authError.message);
  }

  if (!user) {
    throw new Error("Unauthorized: No user logged in");
  }

  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", user.id)
    .single();

  if (profileError) {
    throw new Error("Failed to fetch user role: " + profileError.message);
  }

  const role = profile.role;
  const isAdmin = role === "admin";

  return { role, isAdmin };
}

export async function deleteUser(userId: string, requesterId: string) {
  const supabase = await createClient();

  // Check if the requester is an admin
  const { data: profile, error: profileError } = await supabase
    .from("profiles")
    .select("role")
    .eq("id", requesterId)
    .single();

  if (profileError) {
    throw new Error(
      "Failed to retrieve requester profile: " + profileError.message
    );
  }

  if (profile.role !== "admin") {
    throw new Error("Only admins can delete users.");
  }

  // Proceed with user deletion
  const { error } = await supabase.auth.admin.deleteUser(userId);

  if (error) {
    console.error("Error deleting user:", error.message);
  } else {
    console.log("User deleted successfully");
  }
}
